:root {
  --color-primary: hsl(4, 100%, 67%);
  --color-dark-grey: hsl(234, 29%, 20%);
  --color-charcoal-grey: hsl(235, 18%, 26%);
  --color-grey: hsl(231, 7%, 60%);
  --color-white: hsl(0, 0%, 100%);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: "Roboto", sans-serif;
  width: 100%;
  height: 100%;
}
p {
  font-size: 1.4rem;
  line-height: 1.6;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
.hidden {
  display: none;
}

.app {
  background-color: var(--color-charcoal-grey);
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-content: center;
}
.container {
  max-width: 72rem;
  display: flex;
  border-radius: 1.6rem;
  padding: 1rem;

  color: var(--color-dark-grey);
  background-color: var(--color-white);
}

.left {
  height: 100%;
  width: 60%;
  display: flex;
  color: var(--color-charcoal-grey);
  flex-direction: column;
  justify-content: center;
  padding-left: 4rem;
  padding-right: 5rem;
  box-sizing: border-box;
  font-weight: 700;
  /* gap: 2rem; */
}
.left h1 {
  color: var(--color-dark-grey);

  font-size: 4.5rem;
  font-weight: 700;
  width: 100%;
  margin-bottom: 1rem;
}
.left > p {
  margin-bottom: 1rem;
}
ul {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  gap: 8px;
}
ul li {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 1.2rem;
  font-size: 1.4rem;
}

.right {
  width: 40%;
  height: 100%;
}
.right img {
  width: 100%;
  height: 100%;
}
.right:hover {
  border-radius: 0;
}
form {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 100%;
}
form input {
  padding: 1rem;
  border: 1px solid var(--color-grey);
  border-radius: 5px;
  font-family: inherit;
  outline: none;
  width: 100%;
  margin-bottom: 1rem;
}
form input::placeholder {
  color: var(--color-grey);
}
button {
  width: 100%;
  padding: 1rem;
  color: var(--color-white);
  border-radius: 5px;
  border: none;
  font-weight: 700;
  background-color: var(--color-charcoal-grey);
  font-family: inherit;
}
button:hover {
  background-color: var(--color-primary);
}

.success {
  background-color: var(--color-white);
  width: 38rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 3rem;
}
.success .successIcon {
  font-size: 1rem;
  width: 4rem;
}
.success h1 {
  font-size: 4.1rem;
  color: var(--color-dark-grey);
  font-weight: 700;
}
@media screen and (max-width: 700px) {
  .container {
    width: 55rem;
  }
  .left {
    padding: 2rem;
  }
  ul {
    gap: 0;
  }
  .list-icon {
    width: 1.4rem;
  }
  .left h1 {
    font-size: 3.2rem;
  }
  li .left p {
    margin-bottom: 0;
    font-size: 1rem;
  }
  p {
    font-size: 1.2rem;
  }
  form {
    margin-top: 1rem;
  }
  form input {
    padding: 7px;
    margin-bottom: 0;
  }
}
@media screen and (max-width: 550px) {
  body {
    width: 100%;
    height: 100%;
  }
  .app {
    display: block;
    width: 100%;
    height: 100%;
  }
  .container {
    flex-direction: column-reverse;
    border-radius: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  .left {
    width: 100%;
    padding: 0;
    padding-inline: 3rem;
  }
  .left h1 {
    font-size: 3.5rem;
    margin-block: 3rem;
  }
  .left p {
    font-size: 1.6rem;
  }
  ul {
    gap: 1rem;
  }
  .right {
    width: 100%;
    height: 100%;
  }
  .right img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    object-position: bottom;
  }
  form {
    margin-block: 1rem;
  }
  form label {
    font-size: 1.4rem;
  }
  form input {
    padding: 1.3rem;
    margin-bottom: 1rem;
  }
  form button {
    padding: 1.5rem;
  }
}
